
export default class Persistence{
    saveSelectedAgents(selected_agents){
        localStorage.setItem("selected_agents", JSON.stringify(selected_agents));
    }

    getSelectedAgents(){
        return JSON.parse(localStorage.getItem("selected_agents"));
    }

    saveTrackedUsers(tracked_users){
        localStorage.setItem("tracked_users", JSON.stringify(tracked_users));
    }

    getTrackedUsers(){
        return JSON.parse(localStorage.getItem("tracked_users"));
    }
}